export default function AirportSelectorController($filter) {
    'ngInject';
    return {
        airport:{},
        
        $onInit() {
            this.airportsFiltered = [];
        },
        
        $onChanges(changes) {
            if (changes.airport) {
                this.airport = Object.assign({}, this.airport);
            }
        },

        selectAirport (airport) {
            this.airportsFiltered = [];
            this.onUpdate({
                $event: {
                airport: airport
                }
            });
        },

        getAirportsFiltered(){
            let result = [];

           for (let i = 0; i < this.airportsData.length;i++){
               let airport = this.airportsData[i]
               if (airport.name.toLowerCase().indexOf(this.airport.name.toLowerCase()) !== -1) {
                    result.push(airport);
               }
           }
           return  result;
        },

        keyUp(event){

           

            if (this.airport.name.length > 0) {
                this.searching = true;
                this.airportsFiltered = [];

                this.airportsFiltered = this.getAirportsFiltered(this.airport.name);
    
            } else {
                this.searching = false;
                this.airportsFiltered = [];
            }
        },

        changeText () {

        }

    }
}
